import React from 'react';
import IntlMessages from 'util/IntlMessages';
import ContainerHeader from 'components/ContainerHeader/index';
import CardBox from "components/CardBox/index";
import {Modal, ModalHeader} from 'reactstrap';
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from '@material-ui/icons/Close';
import { ReactFormGenerator, ReactFormBuilder } from 'react-form-builder2';
import 'react-form-builder2/dist/app.css';
import items from './items'
import store from 'react-form-builder2/lib/stores/store';
import TextField from '@material-ui/core/TextField';
import {base_url, $http} from "config";
import {NotificationContainer, NotificationManager} from 'react-notifications';
import MenuItem from '@material-ui/core/MenuItem';
import Button from '@material-ui/core/Button';

export default class AddNew extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      form_id: props.location.state ? props.location.state.id : 0,
      data: props.location.state ? props.location.state.form_data : [],
      previewVisible: false,
      form_type: props.location.state ? props.location.state.form_type : 0,
      form_type_array: [
        {value:0, type:'Location Form', icon:'zmdi-pin'},
        {value:1, type:'Testing Form', icon:'zmdi-chart'},
        {value:2, type:'Commissioning Form', icon:'zmdi-group-work'},
        {value:3, type:'Sign Off', icon:'zmdi-check'}
      ],
      formdata: props.location.state ? props.location.state.form_data : [],
      allforms: [],
      forms: [],
      form_name:  props.location.state ? props.location.state.form_name:'',
    };
    console.log('111',props.location.state)
    console.log('222',this.state.data)
    const update = this._onChange.bind(this);  
    this._onSubmit = this._onSubmit.bind(this);  
    store.subscribe(state => update(state.data));
  }
  showPreview() {
    this.setState({
      previewVisible: true,
    });
  }

  closePreview() {
    this.setState({
      previewVisible: false,
    });
  }

  _onChange(data) {
    this.setState({
      data,
    });
  }

  _onSubmit() {
    this.closePreview(this)
    let form_data = this.state.data
    let form_fields = []
    let label = ''
    form_data.map((item)=>{
      if(item.field_name){
        if(item.label) 
          label = item.label 
        else if(item.content) 
          label = item.content
        else
          label = item.element
        if(item.element ==="Checkboxes")
          item.options.map((row)=>{
            form_fields.push({field_name:row.key,field_type: item.element,field_label:label})
          })
        else if(item.element ==="RadioButtons")
          item.options.map((row)=>{
            form_fields.push({field_name:row.key,field_type: item.element,field_label:label})
          })
        else
          form_fields.push({field_name:item.field_name,field_type: item.element,field_label:label})
      }
    })
    const frmData = new FormData();
    frmData.append('form_id', this.state.form_id);
    frmData.append('form_type', this.state.form_type);
    frmData.append('form_name', this.state.form_name)
    frmData.append('form_data', JSON.stringify(this.state.data));
    frmData.append('form_fields', JSON.stringify(form_fields));

    $http.post(base_url + "form/saveFrom", frmData)
    .then((response) => {
        if (response.data.status==="success") {
            NotificationManager.success("Save new form.", "Success!", 1000, () => {
            });
        } else if (response.data.status === "error") {
            NotificationManager.error(response.data.msg, "Error!", 1000, () => {
            });
        }
    }).catch((error)=>{
        NotificationManager.error(error, "Error!", 1000, () => {
        });
    })

  }

  componentDidMount() {
    var toolboxDiv = document.getElementsByClassName("react-form-builder-toolbar")[0]
    toolboxDiv.style.transition = '0.2s'
    toolboxDiv.onclick = function(e) {
     if(toolboxDiv.style.bottom ==='0px')
      toolboxDiv.style.bottom ='-320px'
     else
      toolboxDiv.style.bottom ='0px'
    }
  }

  selectFrom(data) {
    this.setState({
      form_type:data, 
      forms: this.state.allforms.filter((item) => {
        return item.form_type == data
      }),
      formdata: [],
      form_name: ''
    })
  }

  selectFromName(data) {
    if (data === 0) {
      this.setState({
        formdata: [],
        form_name: ''
      })
    } else {
      let item = this.state.forms.find((item) => {
        return item.id == data
      })
      this.setState({
        formdata: JSON.parse(item.form_data),
        form_name: item.form_name
      })
    }
  }

  enterFormName(data) {
    this.setState({form_name: data})
  }

  render() {
    return (
      <div className="app-wrapper">
        <ContainerHeader match={this.props.match} title={<IntlMessages id="sidebar.form"/>}/>
        <div className="row animated slideInUpTiny animation-duration-3">
          <CardBox styleName="col-12">
              <div>
                <div id="demo-bar" className = "row">
                  <div className = "col-md-12 col-12 text-right mt-2">
                    <button className="btn btn-primary mr-2" onClick={() => this.showPreview()}>Save Form</button>
                  </div>
                  <div className="col-md-4 col-12">
                    <TextField
                        id="location_form"
                        select
                        label="Form Type"
                        value={this.state.form_type}
                        onChange={(event) => {this.selectFrom(event.target.value)}}
                        helperText="Please Select Form Type"
                        margin="normal"
                        fullWidth
                    >
                    {this.state.form_type_array.map(option => (
                        <MenuItem key={option.value} value={option.value}>
                            <i className={`zmdi ${option.icon} zmdi-hc-fw mr-2`}></i>
                            {option.type}
                        </MenuItem>
                    ))}
                    </TextField>
                  </div>
                  
                  <div className="col-md-4 col-12">
                    <TextField 
                    id="form_name"
                    label="New Form Name"
                    value={this.state.form_name}
                    onChange={(event) => {this.enterFormName(event.target.value)}}
                    margin="normal"
                    helperText="Please Enter Form Name"
                    fullWidth
                    />
                  </div>
                </div>
                <div id="form-builder" className="mt-4">
                  <ReactFormBuilder
                  url=''
                  toolbarItems={items}
                  data={this.state.formdata}
                  saveUrl='' />
                </div>
                <Modal key="preview_modal" className="modal-box" toggle={this.closePreview.bind(this)} isOpen={this.state.previewVisible}>
                  <ModalHeader>
                        New Form
                    <IconButton className="text-grey"
                                onClick={this.closePreview.bind(this)}>
                      <CloseIcon/>
                    </IconButton>
                  </ModalHeader>
                  <div className="modal-box-content">
                    <ReactFormGenerator
                      hide_actions
                      data={this.state.data} />
                  </div>
                  <div className="modal-box-footer d-flex flex-row jr-featured-content-right">
                    <Button 
                      color="secondary" variant="contained" 
                      className="jr-btn text-white"
                      onClick={this.closePreview.bind(this)}
                    >
                      Cancel
                    </Button>
                    <Button onClick={this._onSubmit} color="primary" variant="contained" className="jr-btn text-white">
                      Save
                    </Button>
                  </div>
                </Modal> 
              </div>
            </CardBox>
        </div>
        <NotificationContainer/>
      </div>
    );
  }
}